export default {
    name: 'Footer',
    data() {
        return {
            isHomeActive: false
        }
    },
    created() {
        this.isHomeActive = this.$route.name === 'Home' ? true : false;
    },
    methods: {
        toHome() {
            this.$router.replace('home');
        },
        toMy() {
            this.$router.replace('my');
        }
    }
}



